:root {
  --background: #18120b;
  --foreground: #e7b85a;
  --accent: #bfa76a;
  --border: #e7b85a;
  --font-chinese: 'STSong', 'Noto Serif SC', 'SimSun', serif;
}
@media (prefers-color-scheme: dark) {
  :root {
    --background: #0a0a0a;
    --foreground: #ededed;
  }
}
body {
  background: var(--background);
  color: var(--foreground);
  font-family: var(--font-chinese);
  min-height: 100vh;
  margin: 0;
  position: relative;
}
h1, h2, h3, h4, h5, h6 {
  color: var(--foreground);
  font-family: var(--font-chinese);
  letter-spacing: 2px;
}
.chinese-border {
  border: 3px solid var(--border);
  border-radius: 0;
  box-shadow: 0 0 16px 0 #bfa76a44;
  position: relative;
}
.chinese-cloud {
  position: absolute;
  width: 80px;
  height: 40px;
  background: none;
  z-index: 10;
}
.chinese-building {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 220px;
  height: 90px;
  z-index: 10;
}
.chinese-plum {
  position: absolute;
  top: 0;
  right: 0;
  width: 80px;
  height: 80px;
  z-index: 10;
}
.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 24px;
}
